/* Definición de variables para centralizar la paleta de colores */
:root {
  --verde-oscuro: #2e5d47;
  --verde-claro: #a0d6b4;
  --blanco: #eae8e8;
  --azul-acento: #00bcd4;
  --azul-acento-hover: #26c6da;
  font-family: 'Helvetica Neue', sans-serif;
}

/* Contenedor principal de la sección */
.contenedor-seccion {
  max-width: 1600px;
  margin: 0 auto;
  padding: 2rem;
 margin-top: -6vh;
  margin-bottom: 2rem;
}

/* Sección de servicios */
.servicios-iguazu {
  background-color: #0A354C;
  padding: 6rem 2rem;
  color: var(--blanco);
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

