  line-height: 1.6;
  color: var(--color-secundario);
}

.info-personal p {
  font-size: 1.4rem;
  margin-bottom: 1.2rem;
  line-height: 1.6;
  color: var(--color-secundario);
}

/* Sección de beneficios */
.por-que-elegirnos h3 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: var(--color-texto);
}

.lista-beneficios {
  list-style: none;
  padding: 0;
  margin: 0;
}

.lista-beneficios li {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
  color: var(--color-texto);
  font-size: 1.8rem;
  transition: transform 0.3s ease;
}

.lista-beneficios li:hover {
