/* Variables de paleta y tipografía */
:root {
  --clr-fondo-serv: #E2E1DD;
  --clr-card-bg: #ffffff;
  --clr-texto: #333333;
  --clr-texto-sec: #666666;
  --clr-acento: #008080;
  --font-base: "Inter Tight", sans-serif;
  --radius: 0.75rem;
  --transition: 0.3s ease;
}

/* Sección principal */
.nuestros-servicios {
  background-color: var(--clr-fondo-serv);
  padding: 4rem 1rem;
  font-family: var(--font-base);
}

/* Título centrado */
.titulo-servicios {
  text-align: center;
  font-size: 3.5rem;
  margin-bottom: 3rem;
  color: var(--clr-texto);
}

/* Grid de tarjetas: default auto-fit */
.contenedor-servicios {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* A partir de 1024px: exactamente 4 columnas */
@media (min-width: 1024px) {
  .contenedor-servicios {
    grid-template-columns: repeat(4, 1fr);
  }
}
/* Estilo de cada tarjeta */
.servicio {
  background: var(--clr-card-bg);
  border-radius: 1rem;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08);
  padding: 2rem;
  text-align: center;
  transition: transform var(--transition), box-shadow var(--transition);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  opacity: 0;
  transform: translateX(-30px); /* Inicialmente desplazada a la izquierda */
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.servicio.visible {
  opacity: 1;
  transform: translateX(0); /* Se mueve a la posición original */
}

.servicio:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.12);
}

/* Icono centrado y más estilizado */
.icono-servicio {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  margin: 0 auto 1.25rem;
  font-size: 2rem;
  color: #333;
  border-radius: 50%;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* Título de la tarjeta */
.servicio h3 {
  font-size: 2rem;
  margin-bottom: 0.75rem;
  color: var(--clr-texto);
  text-align: center;
}

/* Descripción */
.servicio p {
  font-size: 1.5rem;
  color: var(--clr-texto-sec);
  line-height: 1.6;
  text-align: center;
}

/* Responsive tweaks para pantallas pequeñas */
@media (max-width: 576px) {
  .nuestros-servicios {
    padding: 3rem 0.5rem;
  }
  .contenedor-servicios {
    gap: 1.5rem;
  }
  .servicio {
    padding: 2.5rem 1.5rem;
  }
  .icono-servicio {
    width: 4rem;
    height: 4rem;
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  .servicio h3 {
    font-size: 1.5rem;
  }
  .servicio p {
    font-size: 1rem;
  }
}
/* Ajustes para tablets (768px) */
@media (max-width: 768px) {
  .titulo-servicios {
    font-size: 3rem;
    margin-bottom: 2.5rem;
  }
  .servicio {
    padding: 2rem 1.25rem;
  }
  .icono-servicio {
    width: 4.5rem;
    height: 4.5rem;
    font-size: 2.25rem;
    margin-bottom: 1.5rem;
  }
  .servicio h3 {
    font-size: 1.75rem;
  }
  .servicio p {
    font-size: 1.25rem;
  }
}
/* Ajustes para pantallas grandes (mayores a 1200px) */
@media (min-width: 1200px) {
  .nuestros-servicios {
    padding: 5rem 2rem;
  }
  .titulo-servicios {
    font-size: 4rem;
    margin-bottom: 4rem;
  }
  .contenedor-servicios {
    max-width: 1400px;
    padding: 0 2rem;
  }
  .servicio {
    padding: 2.5rem 2rem;
  }
  .icono-servicio {
    width: 5rem;
    height: 5rem;
    font-size: 3rem;
  }
  .servicio h3 {
    font-size: 2.25rem;
  }
  .servicio p {
    font-size: 1.375rem;
  }
}
